module.exports = "<Form>\n  {['checkbox', 'radio'].map(type => (\n    <div key={`custom-inline-${type}`} className=\"mb-3\">\n      <Form.Check\n        custom\n        inline\n        label=\"1\"\n        type={type}\n        id={`custom-inline-${type}-1`}\n      />\n      <Form.Check\n        custom\n        inline\n        label=\"2\"\n        type={type}\n        id={`custom-inline-${type}-2`}\n      />\n      <Form.Check\n        custom\n        inline\n        disabled\n        label=\"3 (disabled)\"\n        type={type}\n        id={`custom-inline-${type}-3`}\n      />\n    </div>\n  ))}\n</Form>;\n"