module.exports = "<Form>\n  {['checkbox', 'radio'].map(type => (\n    <div key={`custom-${type}`} className=\"mb-3\">\n      <Form.Check // prettier-ignore\n        custom\n        type={type}\n        id={`custom-${type}`}\n        label={`Check this custom ${type}`}\n      />\n\n      <Form.Check\n        custom\n        disabled\n        type={type}\n        label={`disabled ${type}`}\n        id={`disabled-custom-${type}`}\n      />\n    </div>\n  ))}\n</Form>;\n"